import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {ActivityLogComponent} from "./pages/activity-log/activity-log.component";
import {RequestCredentialsComponent} from "./pages/request-credentials/request-credentials.component";
import {AcValidationTokenComponent} from "./acvalidationtokens/acvalidationtoken.component";
import {UserComponent} from "./users/user.component";
import {UserUpsertComponent} from "./users/components/user-upsert/user-upsert.component";

const routes: Routes = [
  {path: '', component: UserComponent},
  {path: 'create', component: UserUpsertComponent},
  {path: ':id/update', component: UserUpsertComponent},
  {path: 'activity-logs', component: ActivityLogComponent},
  {path: 'request-credentials/lockouts', component: RequestCredentialsComponent},
  {path: 'validation-tokens', component: AcValidationTokenComponent},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserRoute {
}
