import {Component} from '@angular/core';
import {IPage} from "../../../../../../base/models/IPage";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../base/services/subscription-container.service";
import * as moment from "moment";
import {TimeUtility} from "../../../../../common/modules/utility/time.utility";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {PageEvent} from "@angular/material/paginator";
import {BaseComponent} from "../../../../../../base/components/base.component";
import {IRequestCredentials} from "../../models/request-credentials.model";
import {ReqCredService} from "../../services/req-cred.service";

@Component({
  selector: 'app-activity-log',
  templateUrl: './request-credentials.component.html',
  styleUrls: ['./request-credentials.component.scss']
})
export class RequestCredentialsComponent extends BaseComponent {
  size: number = 10;
  page: number = 1;
  requestCredentials: IPage<IRequestCredentials> | undefined

  created_from: Date = new Date()
  created_to: Date = new Date()

  constructor(private notifier: NotifierService,
              eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private activatedRoute: ActivatedRoute,
              private reqCredService: ReqCredService) {
    super(eventBus, router, subContainer)
  }


  protected componentName(): string {
    return "RequestCredentialsComponent";
  }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      let from = params['from_date']
      let to = params['to_date']

      if (from != null && to != null) {
        this.created_from = moment(from).toDate();
        this.created_to = moment(to).toDate();
      } else {
        this.created_from = new Date(0)
        this.created_to = new Date()
      }
    });

    this.searchRequestCredentials({
      created_from: this.created_from == null ? this.created_from : TimeUtility.getDayStart(this.created_from),
      created_to: this.created_to == null ? this.created_to : TimeUtility.getDayEnd(this.created_to),
      page: 0, size: this.size,
    })
  }


  onFilterClick() {
    this.searchRequestCredentials({
      created_from: TimeUtility.getDayStart(this.created_from),
      created_to: TimeUtility.getDayEnd(this.created_to),
      page: 0,
      size: this.size,
    })
  }


  private searchRequestCredentials(params: IParams) {
    // console.log(this.username)
    this.loading = true
    const key = this.componentKey(EventKey.REQUEST_CREDENTIALS.ADMIN_SEARCH_LOCKOUTS())
    this.reqCredService.search(key, params)
    this.subscribe(key, null, (value: Boolean, meta: MetaData) => {
      this.requestCredentials = meta.data
      this.loading = false
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.log(meta.data)
    })
  }

  onPageChanged(event: PageEvent) {
    this.searchRequestCredentials({
      created_from: this.created_from,
      created_to: this.created_to,
      page: event.pageIndex,
      size: this.size,
    })
  }

  filterForToday() {
    this.created_from = TimeUtility.getDayStart(new Date())
    this.created_to = TimeUtility.getDayEnd(new Date())

    this.searchRequestCredentials({
      created_from: this.created_from,
      created_to: this.created_to,
      page: 0,
      size: this.size,
    })
  }

  clearFilter() {
    this.created_from = TimeUtility.getDayStart(new Date(0))
    this.created_to = TimeUtility.getDayEnd(new Date())
    this.searchRequestCredentials({
      created_from: this.created_from,
      created_to: this.created_to,
      page: 0,
      size: this.size,
    })
  }
}
