import {Injectable} from "@angular/core";
import {BaseEndpoint} from "../../../../base/endpoints/base.endpoint";
import {IParams} from "../../../../base/endpoints/iparams";
import {environment} from "../../../../../environments/environment";


@Injectable({
  providedIn: 'root'
})
export class UserEndpoints extends BaseEndpoint {

  constructor() {
    super();
  }

  static USERS = {
    USER_ENDPOINT: environment.base_url + '/api/v1/users',
    USER_ENDPOINT_ADMIN: environment.base_url + '/api/v1/admin/users',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.USER_ENDPOINT
    },
    GET(id: number): string {
      return this.USER_ENDPOINT + '/' + id
    },
    UPDATE(id: number): string {
      return this.USER_ENDPOINT + '/' + id
    },
  }
}
